.reviews {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  padding: 70px 110px 80px;
}

.reviews__title {
  font-style: normal;
  font-weight: 600;
  font-size: 28px;
  line-height: 150%;
}

.reviews__row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-column-gap: 22px;
          column-gap: 22px;
}

.review__link {
  width: 170px;
  height: 43px;
  background: #2f80ed;
  border-radius: 58px;
  padding: 12px 40px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: white;
  text-decoration: none;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin: 24px auto 0;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}

.review__link:hover {
  background: rgba(47, 128, 237, 0.1);
  color: #2f80ed;
}

.review__link:active {
  outline: 1px solid #367cff;
}

@media screen and (max-width: 1279px) {
  .reviews {
    padding: 35px 20px 40px;
  }
}

@media screen and (max-width: 767px) {
  .reviews {
    padding: 48px 0 40px;
  }
  .reviews__card {
    padding: 12px 20px;
  }
  .reviews__title {
    margin: 0;
    padding-left: 20px;
    font-size: 28px;
    line-height: 42px;
  }
  .reviews__row {
    padding: 16px 0 0;
    display: -ms-grid;
    display: grid;
    -ms-grid-columns: (minmax(280px, 1fr))[auto-fill];
        grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
    row-gap: 24px;
  }
}
