.reviews__card {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  padding: 8px;
  background: #e8f2ff;
}

.review__image {
  width: 56px;
  height: 47px;
}

.review__card-title {
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 150%;
  margin: 16px 0 12px;
}

.review__card-paragraph {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  margin: 0 0 16px;
  line-height: 150%;
}

.review__author {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-top: auto;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.review__author-avatar {
  margin-right: 17px;
  width: 36px;
  height: 36px;
}

.review__author-name {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
  margin: 0;
}

.review__status {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
  color: #808d9e;
  margin: 8px 0 0;
}

@media screen and (max-width: 767px) {
  .review__card-title {
    font-size: 18px;
    line-height: 27px;
    margin: 16px 0 8px;
  }
  .review__card-paragraph {
    font-size: 12px;
    line-height: 18px;
  }
}
