.courses {
  font-family: "Rubik";
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  padding: 213px 240px;
  margin: 0 auto;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}

.courses__title {
  font-style: normal;
  font-weight: 500;
  font-size: 48px;
  line-height: 57px;
  color: #2c415d;
  margin: 0 0 50px;
}

.courses__table div:first-child {
  margin: 0;
}

.courses__table div:last-child {
  margin: 0;
}

.courses__table {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: (minmax(452px, 1fr))[auto-fill];
      grid-template-columns: repeat(auto-fill, minmax(452px, 1fr));
  gap: 32px;
}

@media screen and (max-width: 1919px) {
  .courses {
    padding: 120px 60px;
  }
  .courses__title {
    font-size: 40px;
  }
  .courses__table {
    gap: 22px;
    -ms-grid-columns: (minmax(452px, 1fr))[auto-fill];
        grid-template-columns: repeat(auto-fill, minmax(452px, 1fr));
  }
}

@media screen and (max-width: 1279px) {
  .courses {
    padding: 100px 60px 70px;
  }
}

@media screen and (max-width: 768px) {
  .courses {
    padding: 105px 20px 40px;
  }
  .courses__table {
    -ms-grid-columns: (minmax(280px, 1fr))[auto-fit];
        grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  }
  .courses__title {
    font-size: 24px;
    line-height: 28px;
  }
}
